import React, { useEffect, useState } from "react";
import {
  Breadcrumb,
  BreadcrumbItem,
  Button,
  Row,
  Col,
  Label,
} from "reactstrap";
import { Link } from "react-router-dom";
import { Errors, Control, Form } from "react-redux-form";
import { actions } from "react-redux-form";
import { useDispatch } from "react-redux";

const required = (val) => val && val.length;
const maxLength = (len) => (val) => !val || val.length <= len;
const minLength = (len) => (val) => val && val.length >= len;
const isNumber = (val) => !isNaN(Number(val));
const validEmail = (val) =>
  /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(val);

const Contact = () => {
  const dispatch = useDispatch();

  const [info, setInfor] = useState({
    firstname: "",
    lastname: "",
    telnum: "",
    email: "",
    agree: false,
    contactType: "Tel.",
    message: "",
  });

  const handleInputChange = (event) => {
    const target = event.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const name = target.name;

    const newInfor = { ...info, [name]: value };
    setInfor(newInfor);
  };

  const handleSubmit = (values, e) => {
    console.log("Current State is: " + JSON.stringify(values));
    alert("Current State is: " + JSON.stringify(values));

    const resetform = dispatch(actions.reset("feedback"));

    console.log("resetform", resetform);
    e.preventDefault();
  };

  return (
    <div className="row row-content">
      <div className="container">
        <div className="row">
          <Breadcrumb>
            <BreadcrumbItem>
              <Link to="/home">Home</Link>
            </BreadcrumbItem>
            <BreadcrumbItem active>Contact Us</BreadcrumbItem>
          </Breadcrumb>
          <div className="col-12">
            <h3>Contact Us</h3>
            <hr />
          </div>
        </div>
        <div className="row row-content">
          <div className="col-12">
            <h3>Location Information</h3>
          </div>
          <div className="col-12 col-sm-4 offset-sm-1">
            <h5>Our Address</h5>
            <address>
              121, Clear Water Bay Road
              <br />
              Clear Water Bay, Kowloon
              <br />
              HONG KONG
              <br />
              <i className="fa fa-phone"></i>: +852 1234 5678
              <br />
              <i className="fa fa-fax"></i>: +852 8765 4321
              <br />
              <i className="fa fa-envelope"></i>:{" "}
              <a href="mailto:confusion@food.net">confusion@food.net</a>
            </address>
          </div>
          <div className="col-12 col-sm-6 offset-sm-1">
            <h5>Map of our Location</h5>
          </div>
          <div className="col-12 col-sm-11 offset-sm-1">
            <div className="btn-group" role="group">
              <a
                role="button"
                className="btn btn-primary"
                href="tel:+85212345678"
              >
                <i className="fa fa-phone"></i> Call
              </a>
              <a role="button" className="btn btn-info">
                <i className="fa fa-skype"></i> Skype
              </a>
              <a
                role="button"
                className="btn btn-success"
                href="mailto:confusion@food.net"
              >
                <i className="fa fa-envelope-o"></i> Email
              </a>
            </div>
          </div>
        </div>
        <div className="col-12">
        <h3>Send us your Feedback</h3>
      </div>
      <div className="col-12 col-md-9">
        <Form
          model="feedback"
          onSubmit={(values, e) => handleSubmit(values, e)}
        >
          <Row className="form-group">
            <Label htmlFor="firstname" md={2}>
              First Name
            </Label>
            <Col md={10}>
              <Control.text
                model=".firstname"
                id="firstname"
                name="firstname"
                placeholder="First Name"
                className="form-control"
                onChange={handleInputChange}
                validators={{
                  required,
                  minLength: minLength(3),
                  maxLength: maxLength(15),
                }}
              />
              <Errors
                className="text-danger"
                model=".firstname"
                onChange={handleInputChange}
                show="touched"
                messages={{
                  required,
                  minLength: "Must be greater than 2 character",
                  maxLength: "Must be 15 character or less",
                }}
              />
            </Col>
          </Row>
          <Row className="form-group">
            <Label htmlFor="lastname" md={2}>
              Last Name
            </Label>
            <Col md={10}>
              <Control.text
                model=".lastname"
                id="lastname"
                name="lastname"
                onChange={handleInputChange}
                placeholder="Last Name"
                className="form-control"
                validators={{
                  required,
                  minLength: minLength(3),
                  maxLength: maxLength(15),
                }}
              />
              <Errors
                className="text-danger"
                model=".lastname"
                show="touched"
                onChange={handleInputChange}
                messages={{
                  required,
                  minLength: "Must be greater than 2 character",
                  maxLength: "Must be 16 character or less",
                }}
              />
            </Col>
          </Row>
          <Row className="form-group">
            <Label htmlFor="telnum" md={2}>
              Contact Tel.
            </Label>
            <Col md={10}>
              <Control.text
                model=".telnum"
                id="telnum"
                name="telnum"
                onChange={handleInputChange}
                placeholder="Tel. number"
                className="form-control"
                validators={{
                  required,
                  minLength: minLength(3),
                  maxLength: maxLength(15),
                  isNumber,
                }}
              />
              <Errors
                className="text-danger"
                model=".telnum"
                onChange={handleInputChange}
                show="touched"
                messages={{
                  required: "Required",
                  minLength: "Must be greater than 2 numbers",
                  maxLength: "Must be 15 numbers or less",
                  isNumber: "Must be a number",
                }}
              />
            </Col>
          </Row>
          <Row className="form-group">
            <Label htmlFor="email" md={2}>
              Email
            </Label>
            <Col md={10}>
              <Control.text
                model=".email"
                id="email"
                onChange={handleInputChange}
                name="email"
                placeholder="Email"
                className="form-control"
                validators={{
                  required,
                  validEmail,
                }}
              />
              <Errors
                className="text-danger"
                model=".email"
                onChange={handleInputChange}
                show="touched"
                messages={{
                  required: "Required",
                  validEmail: "Invalid Email Address",
                }}
              />
            </Col>
          </Row>
          <Row className="form-group">
            <Col md={{ size: 6, offset: 2 }}>
              <Row check>
                <Label check>
                  <Control.checkbox
                    model=".agree"
                    onChange={handleInputChange}
                    name="agree"
                    className="form-check-input"
                  />{" "}
                  <strong>May we contact you?</strong>
                </Label>
              </Row>
            </Col>
            <Col md={{ size: 3, offset: 1 }}>
              <Control.select
                model=".contactType"
                className="form-control"
                onChange={handleInputChange}
                name="contactType"
              >
                <option>Tel.</option>
                <option>Email</option>
              </Control.select>
            </Col>
          </Row>
          <Row className="form-group">
            <Label htmlFor="message" md={2}>
              Your Feedback
            </Label>
            <Col md={10}>
              <Control.textarea
                id="message"
                onChange={handleInputChange}
                model=".message"
                name="message"
                rows="12"
                className="form-control"
              />
            </Col>
          </Row>
          <Row className="form-group">
            <Col md={{ size: 10, offset: 2 }}>
              <Button type="submit" color="primary">
                Send Feedback
              </Button>
            </Col>
          </Row>
        </Form>
      </div>
      </div>

     
    </div>
  );
};

export default Contact;
